import React, { useEffect, useState } from "react";
import { ActionIcon, Button, Table } from "@mantine/core";
import { Edit, Trash } from "tabler-icons-react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

export const UserList = () => {
  const [userData, setUserData] = useState([]);
  console.log("result = ", userData);
  const navigate = useNavigate();
  const addUser = () => {
    navigate("/add-user");
  };

  const onEdit = async (id) => {
    navigate(`/edit-user/${id}`);
  };

  const onDelete = async (id) => {
    console.log("deleteId = ", id);
    const resp = await axios.delete(`http://localhost:5000/user/${id}`);
    if (resp) {
      getData();
    }
  };

  const getData = async () => {
    const resp = await axios.get("http://localhost:5000/user");
    setUserData(resp.data.user);
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="px-20 py-15">
      <div className="text-3xl font-bold underline mt-5 text-lime-500">
        CRUD APPLICATION
      </div>
      <Button
        className="max-w-xs mt-20 mb-5"
        style={{
          backgroundColor: "#46BDE1",
        }}
        onClick={addUser}
      >
        Add NewUser
      </Button>
      <div className="flex flex-col">
        <Table striped>
          <thead>
            <tr>
              <th>No.</th>
              <th>UserName</th>
              <th>Email</th>
              <th>Mobile Number</th>
              <th>Gender</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {userData.map((item, index) => (
              <tr key={item._id}>
                <td>{index + 1}</td>
                <td>{item.name}</td>
                <td>{item.email}</td>
                <td>{item.phone}</td>
                <td>{item.gender}</td>
                <td>
                  <div className="flex flex-row">
                    <ActionIcon
                      onClick={() => {
                        onEdit(item._id);
                      }}
                      color="white"
                    >
                      <Edit style={{ color: "green" }} size={24} />
                    </ActionIcon>
                    <ActionIcon
                      onClick={() => {
                        onDelete(item._id);
                      }}
                      color="white"
                    >
                      <Trash style={{ color: "red" }} size={24} />
                    </ActionIcon>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
    </div>
  );
};
