import React, { useState } from "react";
import { Button, Select, Textarea, TextInput } from "@mantine/core";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AddUser = () => {
  const [input, setInput] = useState({
    name: "",
    email: "",
    gender: "Male",
    phone: "",
    message: "",
    errors: {},
  });

  const navigate = useNavigate();

  const getValueForInput = (incoming) => {
    let value;
    if (incoming.target) {
      if (incoming.target.value !== undefined) {
        value = incoming.target.value;
      }
    } else {
      value = incoming;
    }
    return value;
  };
  const changeHandler = (name) => (inputValue) => {
    const value = getValueForInput(inputValue);
    setInput((data) => ({
      ...data,
      [name]: value,
      errors: { ...input.errors, [name]: "" },
    }));
  };

  const existsAndLength = (value) => value?.length > 3;

  const validateMobile = (phone) => {
    if (isNaN(phone)) {
      return false;
    }
    return !isNaN(parseInt(phone, 10));
  };

  const validateEmail = (email) =>
    String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );

  const validate = () => {
    const keys = {};
    setInput((data) => ({
      ...data,
      errors: {},
    }));
    if (!existsAndLength(input.name)) {
      keys.name = "Please enter 3 or more characters.";
    }
    if (!existsAndLength(input.email) || !validateEmail(input.email)) {
      keys.email = "Please enter correct email.";
    }
    if (!existsAndLength(input.gender)) {
      keys.gender = "Please check value.";
    }
    if (!existsAndLength(input.message)) {
      keys.message = "Please enter 3 or more characters.";
    }

    if (!validateMobile(input.phone) || input.phone.length !== 10) {
      keys.phone = "Please enter 10 digits phone number.";
    }

    if (Object.keys(keys).length) {
      setInput((data) => ({
        ...data,
        errors: keys,
      }));
      return false;
    }
    return true;
  };
  const saveHandler = async () => {
    if (validate()) {
      await axios
        .post("http://localhost:5000/user", {
          name: input.name,
          email: input.email,
          phone: input.phone,
          message: input.message,
          gender: input.gender,
        })
        .catch((e) => {
          console.error(e);
        });
      navigate("/");
    }
  };
  return (
    <div className="mt-5">
      <form className="px-11 py-11">
        <h1 className="text-3xl font-bold underline text-lime-500">
          User Form
        </h1>
        <div className="grid grid-cols-2 gap-x-4 mt-8">
          <TextInput
            label="User Name"
            placeholder="enter your name"
            value={input.name}
            onChange={changeHandler("name")}
            error={input.errors.name}
          />
          <TextInput
            label="Contact email"
            placeholder="enter your email"
            value={input.email}
            onChange={changeHandler("email")}
            error={input.errors.email}
          />
        </div>
        <div className="grid grid-cols-2 gap-x-4 mt-8">
          <Select
            label="Gender"
            placeholder="enter gender"
            value={input.gender}
            onChange={changeHandler("gender")}
            error={input.errors.gender}
            data={["Male", "Female"]}
          />
          <TextInput
            label="Mobile Number"
            placeholder="10 digit mobile number"
            value={input.phone}
            onChange={changeHandler("phone")}
            error={input.errors.phone}
          />
        </div>
        <div className="grid grid-cols-2 gap-x-4 mt-8 ">
          <Textarea
            minRows={6}
            label="Message"
            placeholder="Type your message here..."
            value={input.message}
            onChange={changeHandler("message")}
            error={input.errors.message}
          />
        </div>
        <div className="flex justify-between">
          <Button
            className="max-w-xs mt-20 mb-5"
            style={{
              backgroundColor: "#46BDE1",
            }}
            onClick={() => {
              navigate("/");
            }}
          >
            Back
          </Button>
          <Button
            className="max-w-xs mt-20 mb-5"
            style={{
              backgroundColor: "#46BDE1",
            }}
            onClick={saveHandler}
          >
            Save Data
          </Button>
        </div>
      </form>
    </div>
  );
};

export default AddUser;
